___CSS_LOADER_EXPORT___.push([module.id, `@media (max-width: 768px) {
    .dialog-body {
        width: 32ch;
    }
}

@media (min-width: 768px) {
    .dialog-body {
        width: 60ch;
    }
}

.task-dialog, .create-project-dialog {
    top: 50%;
    left: 50%;
    border-radius: 0.5em;
    justify-content: center;
    border: none;
    outline: none;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    box-shadow: 0px 0px 1em 0.25em rgba(0, 0, 0, 0.4);
    animation: fade-out 0.4s ease-out;
    background-color: var(--background);

    &:open {
        animation: fade-in 0.4s ease-out;
    }

    &:open::backdrop {
        animation: backdrop-fade-in 0.4s ease-out forwards;
        backdrop-filter: blur(2px);
    }
}

.task-dialog form, .create-project-dialog form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1em;
    gap: 1em;
}

.dialog-body {
    display: flex;
    flex-direction: column;
    gap: 1em;

    .priority-input,
    .project-input {
        display: flex;
        align-items: center;
        gap: 0.5em;
    }

    select {
        flex: 1;
        padding: calc(var(--size-bezel) * 0.75) calc(var(--size-bezel) * .5);
        margin: calc(var(--size-bezel) * 0.75 + 3px) calc(var(--size-bezel) * .5);
        background-color: var(--background);
        border-radius: var(--size-radius);
        border: 1px solid var(--text);
    }
}

.dialog-footer {
    align-self: flex-start;
    display: flex;
    gap: 1em;

    button {
        border: none;
        padding: 0.25em 1em;
        border-radius: var(--size-radius);
    }

    #save-task, #save-project {
        background-color: var(--primary);
        color: var(--text);
        cursor: pointer;
        transition: 200ms ease-in-out;

        &:hover {
            background-color: var(--accent);
        }
    }

    #delete-task {
        background-color: var(--red-500);
        color: var(--text);
    }
}

/* Animation keyframes */

@keyframes fade-in {
    0% {
        opacity: 0;
        display: none;
    }

    100% {
        opacity: 1;
        display: block;
    }
}

@keyframes fade-out {
    0% {
        opacity: 1;
        display: block;
    }

    100% {
        opacity: 0;
        display: none;
    }
}

@keyframes backdrop-fade-in {
    0% {
        background-color: rgb(0 0 0 / 0%);
    }

    100% {
        background-color: rgb(0 0 0 / 25%);
    }
}

.input {
    position: relative;

    .input__label {
        position: absolute;
        left: 0;
        top: 0;
        padding: calc(var(--size-bezel) * 0.75) calc(var(--size-bezel) * .5);
        margin: calc(var(--size-bezel) * 0.75 + 3px) calc(var(--size-bezel) * .5);
        white-space: nowrap;
        transform: translate(0, 0);
        transform-origin: 0 0;
        background-color: var(--background);
        transition: transform 120ms ease-in;
        font-weight: bold;
    }

    .input__field {
        display: block;
        width: 100%;
        border: 1px solid currentColor;
        outline: none;
        padding: calc(var(--size-bezel) * 1.5) var(--size-bezel);
        background: transparent;
        border-radius: var(--size-radius);

        &:focus,
        &:not(:placeholder-shown) {
            &+.input__label {
                transform: translate(.25rem, -65%) scale(.8);
                color: var(--color-accent);
            }
        }
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/modal.css"],"names":[],"mappings":"AAAA;IACI;QACI,WAAW;IACf;AACJ;;AAEA;IACI;QACI,WAAW;IACf;AACJ;;AAEA;IACI,QAAQ;IACR,SAAS;IACT,oBAAoB;IACpB,uBAAuB;IACvB,YAAY;IACZ,aAAa;IACb,oDAAoD;IACpD,iDAAiD;IACjD,gDAAgD;IAChD,4CAA4C;IAC5C,iDAAiD;IACjD,iCAAiC;IACjC,mCAAmC;;IAEnC;QACI,gCAAgC;IACpC;;IAEA;QACI,kDAAkD;QAClD,0BAA0B;IAC9B;AACJ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;IACZ,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;;IAER;;QAEI,aAAa;QACb,mBAAmB;QACnB,UAAU;IACd;;IAEA;QACI,OAAO;QACP,oEAAoE;QACpE,yEAAyE;QACzE,mCAAmC;QACnC,iCAAiC;QACjC,6BAA6B;IACjC;AACJ;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,QAAQ;;IAER;QACI,YAAY;QACZ,mBAAmB;QACnB,iCAAiC;IACrC;;IAEA;QACI,gCAAgC;QAChC,kBAAkB;QAClB,eAAe;QACf,6BAA6B;;QAE7B;YACI,+BAA+B;QACnC;IACJ;;IAEA;QACI,gCAAgC;QAChC,kBAAkB;IACtB;AACJ;;AAEA,wBAAwB;;AAExB;IACI;QACI,UAAU;QACV,aAAa;IACjB;;IAEA;QACI,UAAU;QACV,cAAc;IAClB;AACJ;;AAEA;IACI;QACI,UAAU;QACV,cAAc;IAClB;;IAEA;QACI,UAAU;QACV,aAAa;IACjB;AACJ;;AAEA;IACI;QACI,iCAAiC;IACrC;;IAEA;QACI,kCAAkC;IACtC;AACJ;;AAEA;IACI,kBAAkB;;IAElB;QACI,kBAAkB;QAClB,OAAO;QACP,MAAM;QACN,oEAAoE;QACpE,yEAAyE;QACzE,mBAAmB;QACnB,0BAA0B;QAC1B,qBAAqB;QACrB,mCAAmC;QACnC,mCAAmC;QACnC,iBAAiB;IACrB;;IAEA;QACI,cAAc;QACd,WAAW;QACX,8BAA8B;QAC9B,aAAa;QACb,wDAAwD;QACxD,uBAAuB;QACvB,iCAAiC;;QAEjC;;YAEI;gBACI,4CAA4C;gBAC5C,0BAA0B;YAC9B;QACJ;IACJ;AACJ","sourcesContent":["@media (max-width: 768px) {\r\n    .dialog-body {\r\n        width: 32ch;\r\n    }\r\n}\r\n\r\n@media (min-width: 768px) {\r\n    .dialog-body {\r\n        width: 60ch;\r\n    }\r\n}\r\n\r\n.task-dialog, .create-project-dialog {\r\n    top: 50%;\r\n    left: 50%;\r\n    border-radius: 0.5em;\r\n    justify-content: center;\r\n    border: none;\r\n    outline: none;\r\n    -webkit-transform: translateX(-50%) translateY(-50%);\r\n    -moz-transform: translateX(-50%) translateY(-50%);\r\n    -ms-transform: translateX(-50%) translateY(-50%);\r\n    transform: translateX(-50%) translateY(-50%);\r\n    