___CSS_LOADER_EXPORT___.push([module.id, `#sidebar {
    --_background-color: var(--background-color, --background);
    --_text-color: var(--text-color, --text);
    display: flex;
    flex-direction: column;
    height: 100vh;
    box-sizing: border-box;
    width: 400px;
    box-shadow: 1px 0px 20px var(--text);
    padding: 1rem;
    position: sticky;
    top: 0;
    align-self: start;
    font-size: 1.5rem;
    transition: 300ms ease-in-out;
    overflow: hidden;
    text-wrap: nowrap;

    & #toggle-sidebar {
        font-variation-settings: "wght" 400;
    }

    & ul {
        list-style: none;
    }

    & button {
        display: flex;
        align-items: center;
        gap: 1.5rem;
        color: var(--text-color);
        background-color: var(--background-color);
        border: none;
        outline: none;
        padding: 0.5em;
        cursor: pointer;
        border-radius: 4px;
    }

    & .material-symbols-outlined {
        flex-shrink: 0;
    }
}

@media (max-width: 768px) {
    #sidebar {
        height: 80px;
        width: 100%;
        border-right: none;
        border-top: 1px solid var(--text);
        position: fixed;
        top: unset;
        bottom: 0;
        padding: 0;
        display: grid;
        grid-auto-columns: 1fr;
        justify-items: center;
        align-items: center;
        grid-auto-flow: column;
        overflow-x: auto;

        & #profile-header {
            flex-direction: row;
            align-items: flex-start;
        }

        & #navigation,
        .sidebar-actions {
            flex-direction: row;
        }

        button .label,
        h3,
        #view-sidebar,
        .project-list button:not([class~="delete-btn"]) span,
        .drop-down {
            display: none;
        }

        .project-list {
            display: none;
        }

        .project-list.show {
            position: fixed;
            padding: 0;
            bottom: 80px;
            left: 0;
            box-sizing: border-box;
            height: 60px;
            width: 100%;
            display: flex;
            background-color: var(--secondary);

            >div {
                display: flex;
                overflow-x: auto;
            }

            & li {
                display: inline-flex;
                overflow-x: scroll;
            }

            & button {
                width: auto;
                padding: 0.5em 1em;
            }
        }

        .collapse-btn {
            & .material-symbols-outlined {
                transform: rotate(180deg);
                transition: rotate 300ms ease-in;
            }
        }

        .rotate {
            rotate: 180deg;
        }
    }
}

/* width */
::-webkit-scrollbar {
    width: 0.5em;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    --background: var(--background);
    background: var(--background-color);
    border-radius: 4px;
}

@media (min-width: 768px) {
    .collapse-btn {
        & .material-symbols-outlined {
            transition: rotate 300ms ease-in;
        }
    }

    .rotate {
        rotate: -90deg;
    }
}

#sidebar .sidebar-actions button:not([class*="active"]):hover,
#profile-header button:hover,
#add-task button:hover,
.project-container-header button:hover {
    --background-color: var(--secondary);
    --text-color: var(--primary-light);
}

#sidebar.close {
    padding: 5px;
    width: 60px;

    & #toggle-sidebar {
        font-variation-settings: "FILL" 1;
    }

    & .user-actions {
        gap: 0.5em;
    }
}

#profile-header {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    & .user-actions {
        margin-left: auto;
    }
}

.user-profile,
.user-actions {
    display: flex;
}

.user-profile {
    gap: 1rem;
}

#navigation {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#add-task.hide,
#navigation.hide {
    display: none;
}

.project-container-header {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    & h3 {
        margin-right: auto;
    }
}

#add-task button {
    --text-color: var(--primary-dark);
    width: 100%;

    .material-symbols-outlined {
        font-variation-settings: "wght" 700;
    }
}

.sidebar-actions {
    display: flex;
    flex-direction: column;

    & .active {
        --background-color: var(--primary);
        --text-color: var(--primary-dark);
    }

    & button {
        width: 100%;
    }
}

.project-list {
    display: grid;
    transition: 300ms ease-in-out;
    grid-template-rows: 0fr;
    padding-left: 1em;

    li {
        overflow: hidden;
    }

    & .project-container {
        display: flex;
        width: 100%;
        background-color: var(--background-color);

        & :first-child {
            flex: 1;

            & div {
                text-align: left;
                flex: 10;
            }
        }

        & .delete-btn {
            flex-shrink: 0;
            transition: transform 300ms ease-in-out;
        }

        & .delete-btn:hover {
            transform: scale(1.2);
        }

        &:hover {
            --background-color: var(--secondary);
            --text-color: var(--primary-light);
        }

        & button {
            --background-color: transparent;
        }
    }

    & .active {
        --background-color: var(--accent);

        &:hover {
            --background-color: var(--accent);
            --text-color: var(--text-color);
        }
    }
}


.project-list.show {
    grid-template-rows: 1fr;
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/sidebar.css"],"names":[],"mappings":"AAAA;IACI,0DAA0D;IAC1D,wCAAwC;IACxC,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,oCAAoC;IACpC,aAAa;IACb,gBAAgB;IAChB,MAAM;IACN,iBAAiB;IACjB,iBAAiB;IACjB,6BAA6B;IAC7B,gBAAgB;IAChB,iBAAiB;;IAEjB;QACI,mCAAmC;IACvC;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,WAAW;QACX,wBAAwB;QACxB,yCAAyC;QACzC,YAAY;QACZ,aAAa;QACb,cAAc;QACd,eAAe;QACf,kBAAkB;IACtB;;IAEA;QACI,cAAc;IAClB;AACJ;;AAEA;IACI;QACI,YAAY;QACZ,WAAW;QACX,kBAAkB;QAClB,iCAAiC;QACjC,eAAe;QACf,UAAU;QACV,SAAS;QACT,UAAU;QACV,aAAa;QACb,sBAAsB;QACtB,qBAAqB;QACrB,mBAAmB;QACnB,sBAAsB;QACtB,gBAAgB;;QAEhB;YACI,mBAAmB;YACnB,uBAAuB;QAC3B;;QAEA;;YAEI,mBAAmB;QACvB;;QAEA;;;;;YAKI,aAAa;QACjB;;QAEA;YACI,aAAa;QACjB;;QAEA;YACI,eAAe;YACf,UAAU;YACV,YAAY;YACZ,OAAO;YACP,sBAAsB;YACtB,YAAY;YACZ,WAAW;YACX,aAAa;YACb,kCAAkC;;YAElC;gBACI,aAAa;gBACb,gBAAgB;YACpB;;YAEA;gBACI,oBAAoB;gBACpB,kBAAkB;YACtB;;YAEA;gBACI,WAAW;gBACX,kBAAkB;YACtB;QACJ;;QAEA;YACI;gBACI,yBAAyB;gBACzB,gCAAgC;YACpC;QACJ;;QAEA;YACI,cAAc;QAClB;IACJ;AACJ;;AAEA,UAAU;AACV;IACI,YAAY;AAChB;;AAEA,UAAU;AACV;IACI,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA,WAAW;AACX;IACI,+BAA+B;IAC/B,mCAAmC;IACnC,kBAAkB;AACtB;;AAEA;IACI;QACI;YACI,gCAAgC;QACpC;IACJ;;IAEA;QACI,cAAc;IAClB;AACJ;;AAEA;;;;IAII,oCAAoC;IACpC,kCAAkC;AACtC;;AAEA;IACI,YAAY;IACZ,WAAW;;IAEX;QACI,iCAAiC;IACrC;;IAEA;QACI,UAAU;IACd;AACJ;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,mBAAmB;;IAEnB;QACI,iBAAiB;IACrB;AACJ;;AAEA;;IAEI,aAAa;AACjB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;;IAEI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,mBAAmB;;IAEnB;QACI,kBAAkB;IACtB;AACJ;;AAEA;IACI,iCAAiC;IACjC,WAAW;;IAEX;QACI,mCAAmC;IACvC;AACJ;;AAEA;IACI,aAAa;IACb,sBAAsB;;IAEtB;QACI,kCAAkC;QAClC,iCAAiC;IACrC;;IAEA;QACI,WAAW;IACf;AACJ;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,uBAAuB;IACvB,iBAAiB;;IAEjB;QACI,gBAAgB;IACpB;;IAEA;QACI,aAAa;QACb,WAAW;QACX,yCAAyC;;QAEzC;YACI,OAAO;;YAEP;gBACI,gBAAgB;gBAChB,QAAQ;YACZ;QACJ;;QAEA;YACI,cAAc;YACd,uCAA